.all{
     position: absolute;
     top: 25%;
     left: 25%;
     display: grid;
     gap: 60px;
}
.info{
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.name{
    width:600px;
    animation: border 1.5s forwards;
}


.name h1{
    margin: 0;
    font-size:3rem;
    font-weight:300px;
}

.name h2{
    font-weight:100;
    font-size:2rem;
    letter-spacing:3px;
}


.btn{
    font-family: "JetBrains Mono", monospace;
    background-color: black;
    color: #FAF6F0;
    width:170px;
    height:40px;
    font-size:1.5rem;
    font-weight: 100;
    border: none;
    border-radius:10px;
    margin-top:5%;
    cursor: pointer;
}

.detail{
    padding: 5%;
    /* transform: translatex(-150px); */
    animation: detail 1.5s forwards;
}

.imgs{
    cursor: pointer;
    display:grid;
    grid-template-columns:40px 40px;
    gap:0.5%;
}


