  .heading{
      font-weight: bolder;
      font-size: 2rem;
      position: relative;
      left:-10%;
      text-align: center;
      color: #ffbcbc;
      
    }
    .heading:before{
        content:" ";
        display: block;
        height:1px;
        /* width:350px; */
        background: rgb(41, 41, 41);
        position: absolute;
        top:50%;
        left:10%;
      }
    .heading:after{
        content:" ";
        height:1px;
        /* width:350px; */
        background: rgb(66, 66, 66);
        display: block;
        position: absolute;
        top:50%;
        right:10%;
      }
.heading:after,
::before{
    animation: tech  1s  alternate forwards;
}
  @keyframes tech{
    from{
        width: 0;
    }to{
        width: 350px;
    }
  }

  @keyframes detail {

    from{
        transform: translatex(70px);
        opacity: 0;
    }to{
        transform: translatex(10px);
        opacity:1;
    }
    
  }

  @keyframes border {

    from{
        /* border-right:0px solid black ; */
        border: none;
    }
    to{
        border-right:2px solid black ;
    }
    
  }