.navBar{
    display: flex;
    padding: 12px;
    width: 1000px;
    justify-content: space-between;
    border-radius:80px;
    height:40px;
    background-color:#FAF6F0;
}
.title h2{
    font-size:2.5rem;
    margin: 0;
    font-weight: 100;
    vertical-align: middle;
    letter-spacing:3px;
    font-family: "JetBrains Mono", monospace;
}
.lists{
    display: flex;
    width: 400px;
    align-items: center;
    justify-content: space-around;
}

.lists li {
    cursor: pointer;
    list-style: none;
    display: inline;
}